import React, { Component } from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import styled from 'styled-components';



class Navbar extends Component {
  render() {
    const NavContainer = styled.nav`
      text-transform: uppercase;
      background-color: brown;
      display: flex;
      justify-content: center;
      align-items: center;
      position: ${this.props.navPosition};
      top: 0;
      width: 100%;
      color: violet;
      height: 5rem;
    `; 
    
    return (
      <NavContainer id="navbar">
        <AnchorLink 
          style={this.props.styles.home} 
          offset='50' 
          href='#header'>
          HOME
        </AnchorLink>
        <AnchorLink 
          style={this.props.styles.about} 
          offset='50' 
          href='#about'>
          ABOUT
        </AnchorLink>
        <AnchorLink 
          style={this.props.styles.projects} 
          offset='50' 
          href='#projects'>
          PROJECTS
        </AnchorLink>
        <AnchorLink 
          style={this.props.styles.contact} 
          offset='50' 
          href='#contact'>
          CONTACT
        </AnchorLink>
      </NavContainer>
    );
  }
}

export default Navbar;