import React, { Component } from 'react';
import './css/App.css';
import styled from 'styled-components';
import { handleScroll, updateOffsets} from './lib/handleScroll';
import Header from './components/Header';
import Navbar from './components/Navbar';
import About from './components/About';
import Projects from './components/Projects';
import Contact from './components/Contact';





const MainContainer = styled.div`
  text-align: center;
  color: blue;
  font-size: 3rem;
  background-color: palevioletred;
`;

class App extends Component {

  constructor(props) {
    super(props);
    this.state = { 
      navPosition: "static",
      aboutHeight: "60rem",
      aboutPadding: "10rem 5rem 10rem 5rem",
      styles: {
        home: {color: "green"},
        about: {},
        projects: {},
        contact: {}
      }
    };

    this.handleScroll = handleScroll.bind(this);
  }

  

  componentDidMount() {    
    updateOffsets();

    window.addEventListener('scroll', () => {
      this.handleScroll();
    });
  }

  render() {
    return (
      <MainContainer>
        <Header />
        <Navbar navPosition={this.state.navPosition} styles={this.state.styles} />
        <About aboutHeight={this.state.aboutHeight} aboutPadding={this.state.aboutPadding} />
        <Projects />
        <Contact />
      </MainContainer>
    );
  }
}

export default App;